@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap");
@import url(./vendor/swiper-bundle.css);
@import url(./vendor/aos.css);
:root {
  --color-black: #100d0d;
  --color-black-800: #323232;
  --color-white: #fff;
  --color-orange: #f9af31;
  --color-orange-200: #ffe4b7;
  --color-orange-100: #fef7ea;
  --color-gray-800: #777777;
  --color-gray-500: #b2b2b2;
  --color-gray-200: #f7f7f7;
  --color-gray-100: #eaeaea;
  --color-disabled: #d8d8d8;
  --color-red: #cf1515;
  --color-green: #269c47;
  --color-orange-linear: linear-gradient(278.62deg, #fce4bb 1.35%, #fef7ea 113.81%);
  --color-border: rgba(249, 175, 49, 0.3);
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  outline: var(--color-orange);
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

button {
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
}

address {
  font-style: normal;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: var(--color-white);
  color: var(--color-black);
  line-height: 1.3;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}

.error {
  border: 2px #ff4019 solid;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: var(--color-orange-200);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.error {
  border: 2px #ff4019 solid;
}

.site-container {
  overflow-x: hidden;
}

.main {
  flex-grow: 1;
  position: relative;
}

.container,
.wrapper {
  max-width: 1130px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  max-width: 1290px;
}

.logo {
  display: block;
}

.btn {
  display: block;
  background-color: var(--color-black);
  color: var(--color-white);
  border-radius: 10px;
  padding: 15px 30px;
  font-weight: 600;
  position: relative;
  z-index: 0;
  transition: background-color 0.6s ease;
}

.btn:hover {
  background-color: transparent;
}

.btn::before {
  background-color: var(--color-orange);
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  border-radius: 5px;
  transition: all 0.4s ease;
  width: 0;
  height: 100%;
}

.btn:hover::before {
  width: 100%;
}

.btn--reverse {
  background-color: var(--color-orange);
}

.btn--reverse:hover {
  background-color: transparent;
  color: var(--color-orange);
  border-color: var(--color-orange);
}

.btn--reverse::before {
  background-color: transparent;
}

.btn--reverse:hover::before {
  width: 100%;
}

.btn.disable {
  border-color: transparent;
  background-color: transparent;
  color: var(--color-white);
}

