.background {
  position: relative;
}

.background:before {
  content: "";
  position: absolute;
  top: 0;
  border-radius: 20px;
  bottom: 0;
  right: 0;
  background: var(--color-orange-linear);
  left: 0;
  z-index: -1;
  opacity: 0;
  transition: opacity .3s linear;
}

